function decreasePriority({ target }) {
  if (target.nodeName !== 'I') return;
  if (target.parentNode.dataset.action === 'decrease-priority') {
    const li = target.closest('.note-list__item');
    console.log(li);
    const { id } = li.dataset;

    const priority = li.querySelector('.note__priority');
    // console.log(priority);
    const notePriority = priority.textContent;
    console.log(notePriority);
  }
}

export default decreasePriority;
